/*portfolio
=====================*/
.portfolio-slider {
  position: relative;
  padding-right: 50px;
  background: #f39c12;
  /*slick-dots*/
  /*slick-next slick-prev*/
  /*slick-prev*/
  /*slick-next*/
}
.portfolio-slider .slick-dots {
  position: absolute;
  right: 10px;
  top: 60%;
  bottom: 0;
  width: 30px;
  transform: translate(0, -50%);
}
.portfolio-slider .slick-dots li {
  display: block;
  margin: 10px;
}
.portfolio-slider .slick-next,
.portfolio-slider .slick-prev {
  top: auto;
  transform: translate(0, 0);
  bottom: -50px;
  padding: 10px;
  border: 1px solid lightgray;
}
.portfolio-slider .slick-next::before,
.portfolio-slider .slick-prev::before {
  content: '';
  position: relative;
  left: -5px;
  font-family: FontAwesome;
  font-size: 16px;
  line-height: 4px;
  text-align: center;
  color: #0d0d0d;
}
.portfolio-slider .slick-prev {
  left: 45%;
}
.portfolio-slider .slick-prev::before {
  content: '\f053';
}
.portfolio-slider .slick-next {
  right: 45%;
}
.portfolio-slider .slick-next::before {
  content: '\f054';
}
